import Ax from './Ax';

class EventService {

  prepare()
  {
    return Ax.get('/events')
      .then(res=> {
        return Promise.resolve(res.data);
      })
      .catch(err=> {
        return Promise.reject(err);
      })
  }

  getById(id) {
    return Ax.get('/events/'+id)
      .then(res=> Promise.resolve(res.data))
      .catch(err=> Promise.reject(err));
  }

  createPrepare() {
    return Ax.get('/events/create');
  }

  create(params) {
    //private: params.isPrivate?2:1,
    return Ax.post('/events', {
      title: params.title,
      description: params.description,
      due_date: params.dueDate,
      private: params.isPrivate?2:1,
      category_id: params.category
    })
      .then(res=> Promise.resolve(res.data))
      .catch(err=> Promise.reject(err));
  }

  //-------------------------- GUESTS --------------------------------

  addGuest(eventId, ids) {
    return Ax.post('/events/'+eventId+'/guests', {
      guests: ids
    })
      .then(res=> Promise.resolve(res))
      .catch(err=> Promise.reject(err));
  }

  getGuests(eventId) {
    return Ax.get('/events/'+eventId+'/guests')
      .then(res=> Promise.resolve(res.data.data))
      .catch(err=> Promise.reject(err));
  }

  removeGuests(eventId, ids) {
    if (typeof ids == 'number')
      ids = [ids];
    return Ax.delete('/events/'+eventId+'/guests', {
      data: {
        guests: ids
      }
    });
  }

  invititionGuestResponse(eventId, status) {
    // status: 1.Accept , 2.Reject
    return Ax.patch('/invitations', {
      event: eventId,
      accept: status
    });
  }

  // ------------------------------ GALLERY -------------------

  getGallery(eventId) {
    return Ax.get('/events/'+eventId+'/gallery')
      .then(res=> Promise.resolve(res.data))
      .catch(err=> Promise.reject(err));
  }

  addPhotoToGallery(eventId, data) {
    return Ax.post('/events/'+eventId+'/gallery', data)
      .then(res=> Promise.resolve(res.data))
      .catch(err=> Promise.reject(err));
  }

  removePhotoFromGallery(eventId, photoId) {
    return Ax.delete('/events/'+eventId+'/gallery/'+photoId)
      .then(res=> Promise.resolve(res.data.data))
      .catch(err=> Promise.reject(err));
  }

}

export default new EventService();