import EventService from '@/services/event.service';

const initStates = {
  notifications: {
    publics: 0,
    inviteds: 0,
    friends: 0
  },
  events: {
    home: [],
    active: [],
    expired: [],
    viewed: [],
    currentView: null
  }
}

export const event = {
  namespaced: true,
  state: initStates,
  actions: {
    prepare({ commit }) {
      EventService.prepare()
        .then(res=> {
          commit('PREPARE_SUCCESS', res)
        })
    },
    getById({ commit, getters }, eventId) {
      let event = getters.getViewed(eventId);
      if (event !== undefined)
        return Promise.resolve(event)
      else
        return EventService.getById(eventId)
          .then(res=> {
            commit('SET_VIEWED_EVENT', res);
            return Promise.resolve(res);
          });
    },
    addGuest({ commit }, data) {
      return EventService.addGuest(data.eventId, data.guestIds)
        .then(res=> {
          if (res.status == 201) {
            commit('SET_EVENT_GUESTS', {
              eventId: parseInt(data.eventId),
              guests: res.data.data.guests
            });
            return Promise.resolve(res.data)
          }
        })
        .catch(err=> Promise.reject(err));
    }
  },
  mutations: {
    PREPARE_SUCCESS: (state, data) => {
      state.events.home = data
    },
    SET_VIEWED_EVENT: (state, data) => {
      state.events.viewed.push(data);
    },
    SET_EVENT_GUESTS: (state, data ) => {
      state.events.viewed[state.events.viewed.findIndex(el=> el.data.id === data.eventId)].guests= data.guests;
    }
  },
  getters: {
    getViewed: (state) => (id) => {
      return state.events.viewed.find(el=> el.data.id == parseInt(id))
    },
    getViewedIndex: (state) => (id) => {
      return state.events.viewed.findIndex(el=> el.data.id == parseInt(id));
    }
  }
}