import Ax from './Ax';

class ProfileService {

  create(username, dob, name = null, hide_dob = true)
  {
    return Ax.post('/profiles', {
      username,
      dob,
      name,
      hide_dob
    })
      .then(res=> {
        localStorage.setItem('user', JSON.stringify(res.data));
        return Promise.resolve(res.data);
      })
      .catch(err=> {
        return Promise.reject(err);
      })
  }

  get(id) {
    return Ax.get('/profiles/'+id)
      .then(res=> Promise.resolve(res.data))
      .catch(err=> Promise.reject(err));
  }

  follow(userId) {
    return Ax.post('/follows', {follow: userId})
      .then(res=> Promise.resolve(res.data))
      .catch(err=> Promise.reject(err));
  }

  unfollow(userId) {
    return this.followCancel(userId);
  }

  followCancel(userId) {
    return Ax.delete('/follows', {
      data: {
        follow: userId
      }
    })
      .then(res=> Promise.resolve(res.data))
      .catch(err=> Promise.reject(err));
  }

  followRespond(userId, respond) { 
    // respond: 1.Accept , 2.Reject
    return Ax.patch('/follows', {
      follower: userId,
      accept: respond
    })
    .then(res=> Promise.resolve(res.data))
    .catch(err=> Promise.reject(err));
  }



}

export default new ProfileService();