import ax from './Ax'

class AuthService {

  register(phone)
  {
    return ax.post('/users/register', {phone: phone});
  }

  login(phone)
  {
    return ax.post('/users/login', {phone: phone});
  }

  confirm(phone, code)
  {
    return ax.post('/users/confirm', {
      phone: phone,
      code: code
    })
      .then(res=> {
        localStorage.setItem('user', JSON.stringify(res.data.data));
        return Promise.resolve(res.data.data);
      })
      .catch(err=> {
        return Promise.reject(err);
        //console.log(err.response);
      })
  }

  resend(phone)
  {
    return ax.post('/users/confirm/resend', {
      phone: phone
    })
      .then(res=> {
        return Promise.resolve(res);
      })
      .catch(err=> {
        return Promise.reject(err);
      })
  }

  logout()
  {
    localStorage.removeItem('user');
  }
}

export default new AuthService();