<template>
  <div>
    <span v-if="loggedIn && user.profile.username">
      Welcome <strong>{{ user.profile.username }}</strong>
      <br>
      <router-link :to="{name: 'Notifications'}">Notification ({{ count }}) </router-link> | 
    </span>
    <router-link :to="{name: 'TodayBirthdays'}">Explore</router-link> |
    <span v-if="loggedIn">
      <router-link to="/">Home</router-link> | 
      <router-link :to="{name: 'ProfileDetail', params:{id: user.id}}">Profile</router-link> | 
      <router-link :to="{name: 'createEvent'}">Create event</router-link> | 
      <span @click="logout()">Logout</span> 
    </span> 
    <span v-if="!loggedIn">
      <router-link to="/users/register">Register</router-link> | 
      <router-link to="/users/login">Login</router-link>
    </span>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Nav',
  data() {
    return {
      userId: 1
    }
  },
  computed: {
    ...mapState('auth',[
      'loggedIn',
      'user'
    ]),
    ...mapState('notification', [
      'count'
    ])
  },
  created() {
    
  },
  methods: {
    logout() {
      this.$store.dispatch('auth/logout');
      this.$router.push({name: 'Login'});
    }
  }
}
</script>

<style>

#nav {
  padding: 30px;
  text-align: center;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}
</style>