import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/users/register',
    name: 'Register',
    component: () => import('../views/auth/Register')
  },
  {
    path: '/users/login',
    name: 'Login',
    component: () => import('../views/auth/Login')
  },
  {
    path: '/users/confirm',
    name: 'Confirm',
    component: ()=> import('../views/auth/Confirm'),
    props: true
  },
  {
    path: '/profiles/:id',
    name: 'ProfileDetail',
    component: ()=> import('../views/profile/Main'),
    props: true
  },
  {
    path: '/profiles/create',
    name: 'CreateProfile',
    component: () => import('../views/profile/Create')
  },
  {
    path: '/explore',
    name: 'Explore',
    component: () => import('../views/explore/Main'),
    children: [
      {
        path: 'today',
        name: 'TodayBirthdays',
        component: () => import('../views/explore/Today')
      },
    ]
  },
  {
    path: '/events',
    name: 'Event',
    component: () => import('../views/event/Main'),
  },
  {
    path: '/events/create',
    name: 'createEvent',
    component: () => import('../views/event/Create')
  },
  {
    path: '/events/:id',
    name: 'showEvent',
    component: ()=> import('../views/event/Show'),
    props: true,
    children: [
      {
        path: 'guests/add',
        name: 'addGuest',
        component: () => import('../views/event/guest/Add'),
        props: true
      }
    ]
  },
  {
    path: '/events/:id/guests',
    name: 'viewGuests',
    component: () => import('../views/event/Guests'),
    props: true
  },
  {
    path: '/events/:id/gallery',
    name: 'viewGallery',
    component: () => import('../views/event/gallery/Show'),
    props: true
  },
  {
    path: '/notifications',
    name: 'Notifications',
    component: ()=> import('../views/notification/Main')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {

  const publicPages =  [
    '/users/register',
    '/users/login',
    '/users/confirm',
    '/users/resend',
    '/explore/today',
    '/notifications'
  ];

  const noProfileNeedPages = [
    ...publicPages,
    '/profiles/create'
  ];
  
  const authRequired = !publicPages.includes(to.path);
  const user         = JSON.parse(localStorage.getItem('user'));
  const profileNeeded = !noProfileNeedPages.includes(to.path);

  if (authRequired) {
    if (!user)
      next('/users/login');
    else if(profileNeeded && (user.profile.username == null))
      next('/profiles/create')
    else
      next();
  }else
    next();
})

export default router
