import AuthService from '@/services/auth.service';
import ProfileService from '@/services/profile.service';

const user = JSON.parse(localStorage.getItem('user'));
const initStates = user
  ? {loggedIn: true, user}
  : {loggedIn: false, user: null};

export const auth = {
  namespaced : true,
  state: initStates,
  actions: {
    confirm({ commit }, loginData) {
      return AuthService.confirm(loginData.phone, loginData.confirmCode)
        .then(res => {
          commit('CONFIRM_SUCCESS', res);
          return Promise.resolve(res);
        })
        .catch(err => {
          return Promise.reject(err);
        })
    },
    logout({ commit }) {
      AuthService.logout();
      commit('LOGOUT_SUCCESS');
    },
    updateUser({ commit }, data) {
      return ProfileService.create(data.username, data.dob, data.name, data.hide_dob)
        .then(res=> {
          commit('UPDATE_USER', res)
          return Promise.resolve(res);
        })
        .catch(err=> {
          return Promise.reject(err);
        })
    }
  },
  mutations: {
    CONFIRM_SUCCESS: (state, userInfo) => {
      state.loggedIn = true;
      state.user     = userInfo;
    },
    LOGOUT_SUCCESS: (state) => {
      state.loggedIn = false,
      state.user     = null;
    },
    UPDATE_USER: (state, userInfo) => {
      console.log(userInfo);
      state.user = userInfo
    }
  }
}