import axios from 'axios';
import {AuthHeader} from '@/helpers/auth.header';

const remote = axios.create({
  //baseURL: 'http://localhost:8000/api',
  //baseURL: 'http://192.168.0.4/poem/public/api',
  //baseURL: 'http://localhost:3000',
  //baseURL: 'http://127.0.0.1:8000/api',
  baseURL: 'http://api.karo.uk/api',
  //headers: AuthHeader()
  
  //timeout: 2000
});


remote.interceptors.request.use(function(config) {
  config.headers.Authorization = AuthHeader();
  //console.log(config)
  return config;
})

remote.interceptors.response.use(function(response) {
  return response;
}, function(error) {
  //console.log('INTERCEPT', error.response);
  return Promise.reject(error);
}); 



export default remote;

