<template>
  <div>
    <div v-for="event in events" :key="event.id" @click="showEvent(event.id)">
      {{ event.id }}. 
      <strong>{{ loggedInUserId == event.owner_id?'':'Invited' }} </strong> , 
      <strong class="text-danger" v-if="event.expired">Expired</strong> , 
      <span>{{ event.title }}</span> , 
      {{ event.due_date_string }} ,
      <strong>{{ privateStatus[event.private] }}</strong>
      <br>
      <ul>
        <li v-for="guest in event.guests" :key="guest.id">
          <img :src="guest.profile.photo" width="25">
          {{ guest.profile.name }} , <span class="text-secondary">{{ guest.status }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

export default {
  name: 'EventList',
  data() {
    return {
      privateStatus: {
        1: 'Public',
        2: 'Private to followers',
        3: 'private to invites'
      }
    }
  },
  props: {
    events: {
      type: Array
    },
    loggedInUserId: {
      type: Number,
      required: false,
      default: null
    }
  },
  methods: {
    showEvent(eventId) {
      this.$router.push({name: 'showEvent', params: {id: eventId}})
    }
  },
}
</script>
