function AuthHeader() {
  const user = JSON.parse(localStorage.getItem('user'));
  if (user && user.token) {
    return 'Bearer ' + user.token
  }else
    return {}
}

function UserId() {
  const user = JSON.parse(localStorage.getItem('user'));
  if (user && user.id) {
    return user.id
  }else
    return null;
}

export {AuthHeader, UserId}