<template>
  <div id="nav">
    <Nav />
    {{ updateSocket }}
  </div>
  <router-view />

</template>

<script>
import Nav from '@/components/Nav';
import Pusher from 'pusher-js';
import { AuthHeader , UserId } from '@/helpers/auth.header';
import { mapState } from 'vuex';
import _ from 'lodash';

export default {
  components: {
    Nav
  },
  data() {
    return {
      pusher: ''
    }
  },
  mounted() {
    this.$store.dispatch('event/prepare');
    this.$store.dispatch('notification/prepare');
  },
  created() {
    this.pusher = new Pusher('f7db6fa277e53252a471', {
      cluster: 'eu',
      authEndpoint: 'http://api.karo.uk/api/broadcasting/auth',
      auth: {
        headers: {
          Accept: 'application/json',
          Authorization: AuthHeader()
        }
      }
    });
    if (UserId()) {
      this.pusher.subscribe('private-User.'+UserId());
      
    }
    // pusher.subscribe('private-Event.11');
    // pusher.bind('event.guest.respond', data => {
    //   console.log(data);
    // })
  },
  computed: {
    ...mapState('event', ['events']),
    updateSocket() {
      if (_.keys(this.events.home.data).length > 0) {
        this.events.home.data.forEach(e => {
          this.pusher.subscribe(
            'private-'+(e.owner_id == UserId()?'Event.Owner.':'Event.')+e.id
          )
        });
      }
      // this.pusher.bind('Illuminate\\Notifications\\Events\\BroadcastNotificationCreated', data => {
      //   console.log(data);
      // });
      return '';
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 0 10px;
}
</style>
